.hero {
    position: relative;
    overflow: hidden;
    margin-top: -100px;
    height: 100vh;
    @include respond-below(lg) {
        padding-top: 50px;
    }
    &__shapes {
        content: '';
        background-image: url("data:image/svg+xml,%3Csvg width='1919' height='486' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath opacity='.46' d='M1464.12 311.426V95.162l-186.66 108.132 186.66 108.132Z' fill='url(%23a)'/%3E%3Cpath opacity='.46' d='M1919 216.264V0l-189.12 108.132L1919 216.264Z' fill='url(%23b)'/%3E%3Cpath opacity='.46' d='m0 310.264 189.118-108.132L0 94v216.264Z' fill='%234267A1'/%3E%3Cpath opacity='.46' d='m748.387 446.59 186.661-108.132-186.661-108.132V446.59Z' fill='url(%23c)'/%3E%3Cpath opacity='.46' d='M608.5 486V313.969l-149.82 86.019L608.5 486Z' fill='url(%23d)'/%3E%3Cdefs%3E%3ClinearGradient id='a' x1='1370.23' y1='-653.326' x2='1370.23' y2='540.233' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%236CF'/%3E%3Cstop offset='.5' stop-color='%236258A6'/%3E%3Cstop offset='.99' stop-color='%23273470'/%3E%3C/linearGradient%3E%3ClinearGradient id='b' x1='1824.25' y1='-613.324' x2='1824.25' y2='580.235' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%236CF'/%3E%3Cstop offset='.5' stop-color='%236258A6'/%3E%3Cstop offset='.99' stop-color='%23273470'/%3E%3C/linearGradient%3E%3ClinearGradient id='c' x1='840.971' y1='-653.327' x2='840.971' y2='540.232' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%236CF'/%3E%3Cstop offset='.5' stop-color='%236258A6'/%3E%3Cstop offset='.99' stop-color='%23273470'/%3E%3C/linearGradient%3E%3ClinearGradient id='d' x1='532.991' y1='274.918' x2='532.991' y2='1229.86' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%236CF'/%3E%3Cstop offset='.5' stop-color='%236258A6'/%3E%3Cstop offset='.99' stop-color='%23273470'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E");
        position: absolute;
        background-repeat: no-repeat;
        background-position: center;
        background-attachment: fixed;
        background-size: contain;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 0;
    }
    &__bg {
        display: none;
        img:first-of-type {
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;
            top: 0;
            left: 0;
            z-index: -1;
        }
        img:last-of-type {
            position: absolute;
            bottom: 0;
            inset-inline-end: 20%;
        } 
    }
    &__swiper-wrapper  {
        position: relative;
        z-index: 2;
    }
    &__swiper-2 {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        height: 100vh;
        object-fit: cover;
        z-index: -1;
        .swiper-slide {
            position: relative;
            &::after {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background: linear-gradient(180deg, #000000 0%, #252F6B 100%);
                opacity: 0.5;
                z-index: 1;
            }
            &.swiper-slide-visible.swiper-slide-active {
                img {
                    transform: scale(1);
                    transition: all 12s linear 1.5s;
                }
            }
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transform: scale(1.3);
            transition: all 0s linear 4s;
        }
    }
    &__swiper-1 {
        height: 100vh;
    }
    &__content {
        width: 100%;
        height: 100%;
        display: flex;
        gap: 5rem;
        justify-content: center;
        align-items: center;
        &::after {
            content: '';
            position: absolute;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 16.73%, #252F6B 100%);
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 2;
            pointer-events: none;
        }
        &__text {
            max-width: 800px;
            position: relative;
            z-index: 3;
            h3 {
                font-weight: 600;
                font-size: 40px;
                color: #fff;
                text-align: center;
            }
        }
        &__item {
            color: #fff;
            max-width: 520px;
            position: relative;
            z-index: 3;
            h3 {
                font-weight: 600;
                font-size: 40px;
                color: #fff;
            }
            p {
                display: inline-flex;
                align-items: center;
                gap: 10px;
                &::before {
                    content: '';
                    display: inline-block;
                    width: 40px;
                    height: 2px;
                    background: #6459a7;
                }
            }
        }
        img {
            align-self: flex-end;
            max-height: 80vh;
        }
        @include respond-below(lg) {
            padding-top: 3rem;
            gap: 2rem;
            flex-wrap: wrap;
            padding-inline: 1rem;
            &__text {
                h3 {
                    font-size: 1.75rem;
                    text-align: justify;
                    @include respond-below(sm) {
                        font-size: 1.375rem;
                    }
                }
            }
            &__item {
                h3 {
                    font-size: 1.5rem;
                }
                p {
                    &::before {
                        content: '';
                        width: 20px;
                    }
                }
            }
        }
    }
    &__swiper-btn {
        position: relative;
        z-index: 3;
        @include center('h');
        bottom: 80px;
        width: 100%;
        .slide-button {
            color: #fff;
            .icon--arrow-right::before {
                content: '';
                background-image: url("data:image/svg+xml,%3Csvg width='48' height='18' viewBox='0 0 48 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M38.4983 17.9994L37.3171 16.7901L44.2638 9.84342H0V8.15597H44.2638L37.3171 1.20933L38.4983 0L47.498 8.9997L38.4983 17.9994Z' fill='%23fff'/%3E%3C/svg%3E%0A");
                @include ltr() {
                    background-image: url("data:image/svg+xml,%3Csvg width='48' height='18' viewBox='0 0 48 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.50166 17.9994L10.6829 16.7901L3.73623 9.84342H48V8.15597H3.73623L10.6829 1.20933L9.50166 0L0.501961 8.9997L9.50166 17.9994Z' fill='%23fff'/%3E%3C/svg%3E%0A");
        
                }
            }
            .icon--arrow-prev::before {
                content: '';
                background-image: url("data:image/svg+xml,%3Csvg width='48' height='18' viewBox='0 0 48 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.50166 17.9994L10.6829 16.7901L3.73623 9.84342H48V8.15597H3.73623L10.6829 1.20933L9.50166 0L0.501961 8.9997L9.50166 17.9994Z' fill='%23fff'/%3E%3C/svg%3E%0A");
                @include ltr() {
                    background-image: url("data:image/svg+xml,%3Csvg width='48' height='18' viewBox='0 0 48 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M38.4983 17.9994L37.3171 16.7901L44.2638 9.84342H0V8.15597H44.2638L37.3171 1.20933L38.4983 0L47.498 8.9997L38.4983 17.9994Z' fill='%23fff'/%3E%3C/svg%3E%0A");
        
                }
            }
        }   
    }
}
.about {
    &::after {
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        position: absolute;
        background: linear-gradient(180deg, var(--dark-blue), #12100C);
        opacity: 0.85;
        z-index: -1;
    }
    padding: 120px 0;
    position: relative;
    background: url("../images/about.jpg") no-repeat center center;
    background-size: cover;
    z-index: 1;
    background-attachment: fixed;
    overflow: hidden;
    @include respond-below(sm) {
        padding: 50px 0;
    }
    &__swiper {
        overflow: visible;
    }
    &__swiper-wrapper {
        text-align: center;
        max-width: 850px;
        margin: 0 auto;
        .swiper-pagination {
            position: static;
            display: flex;
            justify-content: center;
            gap: 1.875rem;
            margin-bottom: 90px;
            @include respond-below(md) {
                flex-wrap: wrap;
            }
            @include respond-below(sm) {
                display: block;
                margin-bottom: 70px;
            }
        }
        .swiper-pagination-bullet {
            display: inline-flex;
            min-width: 185px;
            border: 1px solid #fff;
            background: transparent;
            justify-content: center;
            align-items: center;
            border-radius: 1.875rem;
            height: auto;
            color: #fff;
            font-size: 1.375rem;
            opacity: 1;
            padding: 10px 0 16px;
            transition: all ease-in-out 0.25s;
            &:hover {
                background: rgba(#fff, 0.3);
                border-color: transparent
            }
            @include respond-below(sm) {
                display: block;
                margin: 0 auto !important;
                font-size: 1rem;
                &:not(:last-child) {
                    margin-bottom: 0.75rem !important;
                }
            }
        }
        .swiper-pagination-bullet-active {
            background: rgba(#fff, 0.1);
            border-color: transparent
        }
    }
    &__title {
        font-size: 1.875rem;
        color: #fff;
        font-weight: 700;
        line-height: 50px;
        margin-bottom: 90px;
        @include respond-below(sm) {
            font-size: 1.5rem;
            line-height: 40px;
            margin-bottom: 70px;
        }
    }
}

.statistics {
    padding: 120px 0;
    background: url("../images/statistics-bg.jpg") no-repeat center center;
    background-size: cover;
    position: relative;
    z-index: 1;
    &__shapes {
        content: '';
        background: url("../images/statistics-shapes.svg") no-repeat center center;
        background-size: contain;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        pointer-events: none;
        background-attachment: fixed;
    }
    &__header {
        text-align: center;
        margin-bottom: 110px;
        & > p {
            font-size: 14px;
            color: #2A346C;
            margin-bottom: 1.875rem;
        }
    }
    &__body {
        text-align: center;
        
    }
    &__title {
        font-size: 1.875rem;
        font-weight: 700;
        color: #2A346C;
        margin-bottom: 1.25rem;
    }
    &__item {
        padding: 2.5rem 1.875rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: linear-gradient(to right, var(--dark-blue), var(--primary-hover));
        border-radius: 20px;
        color: #FFFFFF;
        @include respond-below(xxl) {
            padding: 2.5rem 1.25rem;
        }
        &__num {
            color: #fff;
            margin: 0;
            font-size: 1rem;
            font-weight: 700;
            text-align: start;
            padding-inline-end: 3px;
            span {
                font-size: 1.875rem;
                margin-bottom: 10px;
                display: block;
                line-height: 1;
            }
        }
    }
    &__swiper-wrapper{
        margin-top: 4.375rem;
        .slide-button-wrapper {
            margin-top: 4rem;
        }
    }
}

.registration {
    background: url("../images/registeration-bg.png") no-repeat center center;
    background-size: cover;
    position: relative;
    background-attachment: fixed;
    display: flex;
    z-index: 1;
    overflow: hidden;
    @include respond-below(xl) {
        display: block;
    }
    &::before {
        content: '';
        width: 100%;
        height: 100%;
        background: url("../images/registration-shape.png") no-repeat center center;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        pointer-events: none;
    }
    &__guide {
        flex: 0 0 585px;
        padding: 120px 0;
        color: #FFFFFF;
        display: flex;
        flex-direction: column;
        gap: 60px;
        position: relative;
        z-index: 1;
        @include respond-below(xxxl) {
            flex: 0 0 470px
        }
        @include respond-below(xxl) {
            flex: 0 0 370px;
        }
        @include respond-below(xl) {
            flex-direction: row;
        }
        @include respond-below(md) {
            flex-direction: column;
            align-items: center;
            padding: 70px 20px !important;
        }
        @include respond-below(sm){
            text-align: center;
        }
        &::before {
            content: '';
            width: 100%;
            height: 100%;
            background: linear-gradient(30deg, #6258A6, #0A1233);
            position: absolute;
            top: 0;
            z-index: -1;
            opacity: 0.5;
            @include rtl() {
                right: 0;
            }
            @include ltr() {
                left: 0;
            }
        }
        
        @include rtl() {
            padding-right: 110px;
            padding-left: 90px;
            @include respond-below(xxxl) {
                padding-right: 80px;
                padding-left: 40px;
            }
            @include respond-below(xxl) {
                padding-right: 30px;
                padding-left: 10px;
            }
        }
        @include ltr() {
            padding-left: 110px;
            padding-right: 90px;
            @include respond-below(xxxl) {
                padding-left: 80px;
                padding-right: 40px;
            }
            @include respond-below(xxl) {
                padding-left: 30px;
                padding-right: 10px;
            }
           
        }
        &__item {
            @include respond-below(xl) {
                flex-grow: 1;
                width: 50%;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
            }
            @include respond-below(lg) {
                width: 100%;
                display: block;
            }
            h3 {
                font-size: 25px;
                margin-bottom: 20px;
                font-weight: 700;
                width: 95%;
                padding: 0;
                @include respond-below(sm) {
                    font-size: 22px;
                }
            }
            p {
                font-size: 14px;
                font-weight: 400;
                margin:  0 0 30px;
                line-height: 30px;
                @include respond-below(xl) {
                    margin-top: auto;
                }
            }
            .button {
                padding: 10px 25px;
                font-size: 14px;
            }
        }

    }
    &__forms {
        flex-grow: 1;
        transition: ease-in-out 0.5s all;
        position: relative;
        z-index: 1;
        padding: 65px 0;
        overflow: hidden;
        .swiper {
            overflow: visible;
        }
        .swiper-slide {
            width: auto !important;
        }
        .swiper-slide:nth-child(2n) {
            margin-top: 50px;
        }
        @include rtl() {
            padding-right: 48px;
            @include respond-below(sm) {
                padding-right: 25px;
            }
        }
        @include ltr() {
            padding-left: 48px;
            @include respond-below(sm) {
                padding-left: 25px;
            }
        }
        &::before {
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: linear-gradient(180deg, var(--dark-blue), #12100C);
            opacity: 0.3;
            z-index: -1;
        }
        &::after {
            content: '';
            width: 55px;
            height: 100%;
            position: absolute;
            top: 0;
            z-index: 9;
            @include rtl (){
                right: 0;
                background: linear-gradient(270deg, rgba(0, 0, 0, 0.27), rgba(0, 0, 0, 0));
            }
            @include ltr() {
                left: 0;
                background: linear-gradient(-270deg, rgba(0, 0, 0, 0.27), rgba(0, 0, 0, 0));
            }
        }
        .slide-button-wrapper {
            margin-top: 100px;
        }
        .card-wrapper {
            width: 320px;
            transform-style: preserve-3d;
            display: block;
            position: relative;
            height: 473px;
            transition: all 0.25s ease-in-out;
            background: transparent;
            perspective: 1000px;
            @include respond-below(sm) {
                width: 270px;
            }
            &:hover {
                .registration__forms__card {
                    transform: rotateY(180deg);
                }
            }
        }
        &__card {
            position: relative;
            width: 100%;
            height: 100%;
            transition: transform 1s cubic-bezier(0.3, 0.41, 0, 1.24);
            transform-style: preserve-3d;
            border-radius: 35px;            
            &__front {
                &::after {
                    content: "";
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    left: 0;
                    top: 0;
                    background: linear-gradient(to top, #263570 0%, rgba(18, 16, 12, 0) 100%);
                    z-index:  -2;
                }
                backface-visibility: hidden;
                overflow: hidden;
                padding: 30px;
                width: 100%;
                height: 100%;
                background-position: center center;
                border-radius: 30px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                transition: ease-in-out 0.5s all;
                align-items: flex-start;
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
                position: absolute;
                color: #FFFFFF;
                z-index: 1;
                
                &__title {
                    display: flex;
                    justify-content: space-between;
                    gap: 40px;
                    width: 100%;

                    h4 {
                        width: 60%;
                    }
                    
                    .slide-button--prev {
                        &::before {
                            background: #FFFFFF;
                            z-index: 8;
                        }
                        .icon--arrow-prev {
                            &::before {
                                background-image: url("data:image/svg+xml,%3Csvg width='48' height='18' viewBox='0 0 48 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.50166 17.9994L10.6829 16.7901L3.73623 9.84342H48V8.15597H3.73623L10.6829 1.20933L9.50166 0L0.501961 8.9997L9.50166 17.9994Z' fill='white'/%3E%3C/svg%3E%0A");        display: inline-block;
                                width: 48px;
                                height: 18px;
                                background-size: contain;
                                background-repeat: no-repeat;
                                vertical-align: middle;
                                position: absolute;
                                top: 50%;

                                transform: translatey(-50%);
                                @include rtl() {
                                    left: 50%;
                                }
                                @include ltr() {
                                    background-image: url("data:image/svg+xml,%3Csvg width='48' height='18' viewBox='0 0 48 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M38.4983 17.9994L37.3171 16.7901L44.2638 9.84342H0V8.15597H44.2638L37.3171 1.20933L38.4983 0L47.498 8.9997L38.4983 17.9994Z' fill='white'/%3E%3C/svg%3E%0A");        display: inline-block;
                                    right: 50%;
                                }
                            }
                        }
                    }
                }
            }
            &__back {
                position: absolute;
                overflow: hidden;
                backface-visibility: hidden;
                transform: rotateY(180deg);
                z-index: 2;
                padding: 30px;
                height: 100%;
                transition: ease-in-out 0.5s all;
                width: auto;
                border-radius: 30px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: flex-start;
                background-color: #FFFFFF;
                
                &__content {
                    p {
                        color: #303B6F;
                        font-size: 14px;
                        font-weight: 400;
                    }
                }
                .button--purple {
                    border-radius: 10px;
                    display: inline-flex;
                    width: 100%;
                    .icon--arrow-prev {
                        &::before {
                            content: '';
                            background-image: url("data:image/svg+xml,%3Csvg width='26' height='18' viewBox='0 0 26 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.50166 17.9994L10.6829 16.7901L3.73623 9.84342H26V8.15597H3.73623L10.6829 1.20933L9.50166 0L0.501961 8.9997L9.50166 17.9994Z' fill='white'/%3E%3C/svg%3E%0A");
                            width: 25px;
                            height: 18px;
                            display: inline-block;
                            background-repeat: no-repeat;
                            background-size: contain;
                            @include ltr() {
                                background-image: url("data:image/svg+xml,%3Csvg width='26' height='18' viewBox='0 0 26 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M16.8231 18L15.6187 16.7906L22.7021 9.84375H0V8.15625H22.7021L15.6187 1.20937L16.8231 0L26 9L16.8231 18Z' fill='white'/%3E%3C/svg%3E%0A");
                            }
                        }
                    }
                    
                }

                

            }
        }
        h4 {
            font-size: 14px;
            font-weight: 400;
            span {
                font-size: 25px;
                font-weight: 700;
                display: block;
            }
        }
        img {
            width: 50px;
            height: 50px;
        }
        .slider-transition-next {
            .registration__forms__card {
                @include rtl() {
                    transform: rotateY(30deg);
                }
                @include ltr() {
                    transform: rotateY(-30deg);
                }
            }
        }
        .slider-transition-prev {
            .registration__forms__card {
                @include rtl() {
                    transform: rotateY(-30deg);
                }
                @include ltr() {
                    transform: rotateY(30deg);
                }
            }
        }
    }
}