.about-office {
    max-width: 1000px;
    margin: 0 auto;
    &__box {
        background: #fff;
        border: 1px solid #EFF1FA;
        box-shadow: 0px 3px 25px rgba(#000, 0.03);
        position: relative;
        z-index: 1;
        overflow: hidden;
        &__title {
            color: var(--primary-hover);
            font-size: 25px;
            margin-bottom: 15px;
        }
        p {
            color: var(--primary);
            font-size: 0.875rem;
            line-height: 24px;
            margin: 0;
        }
        &.large-box {
            display: flex;
            margin-bottom: 1.25rem;
            padding-left: 2.75rem;
            align-items: flex-end;
            @include ltr() {
                flex-direction: row-reverse;
            }
            @include respond-below(lg) {
                flex-direction: column;
                padding-left: 0;
                align-items: center;
                @include ltr() {
                    flex-direction: column;
                }
            }
            .office-manager-photo {
                flex: 0 0 250px;
                position: relative;
                z-index: 1;
                display: flex;
               flex-direction: column;
               justify-content: flex-end;
               @include respond-below(lg) {
                width: 200px;
                margin-inline: auto;
               }
               img {
                width: 100%;
               }
                &::after {
                    content: '';
                    background: url("../images/office-shape2.png");
                    width: 90px;
                    height: 138px;
                    position: absolute;
                    bottom: -15px;
                    right: -25px;
                    z-index: -1;
                }
                &::before {
                    content: '';
                    background: url("../images/office-shape1.png");
                    width: 110px;
                    height: 170px;
                    position: absolute;
                    top: 0;
                    left: -2.75rem;
                    z-index: -1;
                }
            }
            .large-box-info {
                padding: 1.875rem;
                flex-grow: 1;
                @include respond-below(sm) {
                    padding: 1rem;
                }
            }
        }
        &.small-box {
            padding: 2.5rem 0;
            padding-inline-start: 80px;
            padding-inline-end: 1.875rem;
            @include respond-below(lg) {
                margin-bottom: 1.25rem;
            }
            @include respond-below(sm) {
                padding: 1.25rem 1.875rem;
            }
            &::before {
                content: '';
                display: block;
                border-top: 35px solid transparent;
                border-bottom: 35px solid transparent;
                border-inline-start: 60px solid transparent;
                position: absolute;
                top: 25px;
                inset-inline-start: 0;
                @include respond-below(sm) {
                    border-top: 10px solid transparent;
                    border-bottom: 10px solid transparent;
                    border-inline-start: 20px solid transparent;
                    top: 30px;
                }
            }
            &.light-blue::before {
                border-inline-start-color: #65A7E2;
            }
            &.dark-blue::before {
                border-inline-start-color: var(--primary-hover)
            }
        }
    }
}