.search-result {
  padding-top: 1.5rem;
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.5rem;
    @include respond-below(xxxl) {
      grid-template-columns: repeat(3, 1fr);
    }
    @include respond-below(xl) {
      grid-template-columns: repeat(2, 1fr);
    }
    @include respond-below(md) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__list-item {
    padding: 1.5rem;
    background-color: #FFF;
    border-radius: 3px;
    box-shadow: 0 4px 86px rgba(#000, .1);
    display: flex;
    gap: 1rem;
    @include respond-below(lg) {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__logo {
      display: flex;
      flex: 90px 0 0;
      width: 90px;
      height: 90px;
      border-radius: 50%;
      border: 5px solid #EFEFEF;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      img {
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
      }
    }
    &__title {
      font-size: 1.25rem;
      margin-bottom: 1rem;
      margin-top: 4px;
    }
    &__list{
      display: flex;
      gap: 1.5rem;
      margin-top: auto;
      p {
        margin-bottom: 0;
        color: #989FB2;
        svg {
          display: inline-block;
          margin-inline-end: .5rem;
        }
      }
    }
  }
}

.search-404 {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 1.125rem;
  color: #989FB2;
  p {
    margin-top: 1rem;
  }
}

.search-results {
  padding: 1.875rem 0;
  &__title {
    text-align: center;
    color: #989FB2;
    font-size: 1.25rem;
    margin-bottom: 2.5rem;
    font-weight: 400;
  }
  &__item {
    border-bottom: 1px solid #F0F0F0;
    margin-bottom: 1.875rem;
    padding-bottom: 0.875rem;
  }
  &__name {
    font-size: 1rem;
    color: #1D252D;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 1.125rem;
  }
  &__link {
    color: #989FB2;
    font-size: 0.875rem;
    transition: all 0.25s ease-in-out;
    &:hover {
      color: #777;
    }
  }
}
.pagination {
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
  
  &__button {
    width: 38px;
    height: 38px;
    flex: 0 0 38px;
    background: transparent;
    border: 1px solid var(--sliver);
    border-radius: 4px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    transition: all 0.25s ease-in-out;
    cursor: pointer;
    &:hover {
      background: var(--dark-blue);
      border-color: var(--dark-blue);
    }
    &::before {
      content: '';
      display: inline-block;
      vertical-align: middle;
      width: 12px;
      height: 12px;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      @include ltr() {
        transform: rotate(180deg);
      }
    }
    &[disabled] {
      background: #E3E4E9;
      border-color: #E3E4E9;
      pointer-events: none;
    }
  }
  &__prev-btn::before {
    background-image: url("data:image/svg+xml,%3Csvg width='9' height='12' viewBox='0 0 9 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.839844 1.41L5.41984 6L0.839844 10.59L2.24984 12L8.24984 6L2.24984 0L0.839844 1.41Z' fill='%23C4CDD5'/%3E%3C/svg%3E%0A");
  }
  &__next-btn::before {
    background-image: url("data:image/svg+xml,%3Csvg width='9' height='12' viewBox='0 0 9 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.16016 1.41L3.58016 6L8.16016 10.59L6.75016 12L0.750156 6L6.75016 0L8.16016 1.41Z' fill='%23C4CDD5'/%3E%3C/svg%3E%0A");

  }
  &__page-num {
    width: 38px;
    height: 38px;
    flex: 0 0 38px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #DFE3E8;
    border-radius: 4px;
    font-size: 0.875rem;
    color: #212B36;
    &.current-page {
      background: var(--dark-blue);
      color: #fff;
      border-color: var(--dark-blue);
    }
  }
}