.agency {  
    &__profile--wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #FFF;
        padding: .75rem;
        border: 1px solid #C5C5C5;
        border-radius: 3px;
        &.disabled {
            background-color: #ECECEC;
            border-color: transparent;
        }
    }
    &__photo-actions {
        display: flex;
        flex-direction: column;
        gap: .5rem;
        a {
            display: inline-flex;
            width: 30px;
            height: 30px;
            align-items: center;
            justify-content: center;
            box-shadow: 0 10px 25px rgba(#000, .25);
            border-radius: 50%;
            border: 1px solid transparent;
            background-color: #FFF;
            svg {
                width: 16px;
                height: 16px;
            }
        }
    }
    &__photo-actions-wrapper {
        display: flex;
        align-items: center;
    }
    &__photo-wrapper {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        border: 2px solid #EFEFEF;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0.625rem;
        position: relative;
        background-color: #FFF;
        @include respond-below(sm) {
            width: 70px;
            height: 70px;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 50%;
        }

    }
    &__profile-photo {
        margin: 0;
        cursor: pointer;
        width: 26px;
        height: 26px;
        background: #989FB2;
        border-radius: 50%;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        inset-inline-start: 0;
        bottom: 0;
        @include respond-below(sm) {
            width: 22px;
            height: 22px;
        }
        input {
            display: none;
        }
    }
}