
.main-tags  {
    display: flex; 
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
    margin: 0.125rem auto;
    max-width: 480px;
    a {
        padding: 0.75rem 0.5rem;
        color: #fff;
        text-decoration: none;
        font-size: 14px;
        font-weight: 400;
        text-align: center;
        border: 1px solid #fff;
        background: transparent;
        transition: background 0.25s ease-in-out, border 0.25s ease-in-out;
        &:hover {
            background: #fff;
            color: var(--primary);
        }
    }
    &--colored {
        max-width: unset;
        a {
            background: #989FB2;
            color: #F3F5FB;
            font-weight: 400;
            font-size: 18px;
            &:hover {
                background: var(--primary);
                color: #fff;
            }
        }
    }
}
.main-tabs {
    gap: 1.25rem;
    align-items: flex-start;
    position: relative;
    @include respond-below(lg) {
        flex-direction: column;
    }
    .nav {
        flex-direction: column;
        padding: 1.5rem 1.2rem;
        background: #fff;
        box-shadow: 0px 3px 25px rgba(#000, 0.05);
        @include respond-above (lg) {
            position: sticky;
            top: 1rem;
        }
        h5 {
            color: var(--primary);
            margin-bottom: 2rem;
            text-align: center;
            font-weight: 600;
            @include respond-below(lg) {
                flex: 0 0 100%;
            }
        }
        @include respond-below(lg) {
            flex-direction: row;
            gap: 0.875rem;
            justify-content: space-between;

        }
    }
    .nav-link {
        padding: 0.875rem;
        color: var(--primary);
        background: #fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;
        font-size: 1rem;
        font-weight: 600;
        width: 250px;
        transition: color 0.25s ease-in-out, background 0.25s ease-in-out;
        text-align: start;
        &:not(:last-of-type) {
            margin-bottom: 0.875rem;
        }
        &.active {
            background: var(--primary-hover);
            color: #fff;
            &::after {
                background-image: url("data:image/svg+xml,%3Csvg width='9' height='16' viewBox='0 0 9 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.5 15.9883L0 8.48828L7.5 0.988281L8.93382 2.44049L2.88603 8.48828L8.93382 14.5361L7.5 15.9883Z' fill='%23fff'/%3E%3C/svg%3E%0A");
            }
        }
        &::after {
            content: '';
            width: 0.6rem;
            height: 1rem;
            background-image: url("data:image/svg+xml,%3Csvg width='9' height='16' viewBox='0 0 9 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.5 15.9883L0 8.48828L7.5 0.988281L8.93382 2.44049L2.88603 8.48828L8.93382 14.5361L7.5 15.9883Z' fill='%232A346C'/%3E%3C/svg%3E%0A");
            background-repeat: no-repeat;
            transition: background-image 0.25s ease-in-out ;
            @include respond-below(lg) {
                display: none;
            }
            @include ltr() {
                transform: rotate(180deg);
            }
        }
        @include respond-below(lg) {
            padding: 0.5rem;
            text-align: center;
            width: 20%;
            display: block;
            &:not(:last-of-type) {
                margin-bottom: 0;
            }
        }
        @include respond-below(md) {
            width: 30%;
        }
        @include respond-below(sm) {
            width: 100%;
        }
    }
    .tab-content {
        width: 100%;
    }
}
