._certificate {
    margin: 0 auto;
    padding: 50px 100px;
    max-width: 1025px;
    background:url("../images/certificate-bg.png") no-repeat center center,  #F1F1F1;
    @include respond-below(md) {
        padding: 50px 30px;
    }
    @include respond-below(sm) {
        padding: 50px 10px;
    }
    &__title {
        font-size: 2.375rem;
        color: var(--primary);
        text-align: center;
        font-weight: 400;
        margin-bottom: 20px;
        @include respond-below(lg) {
            font-size: 1.5rem;
        }
    }
    p {
        color: #4E4C4D;
        font-size: 0.875rem;
        margin-bottom: .625rem;
        span {
            color: #252F6B;
        }
        &.small-text {
            font-size: 0.875rem;
            margin-bottom: 5px;
        }
        &.xs-text {
            margin: 0;
            font-size: 0.75rem;
        }
    }
    &__photo {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        border: 1px solid #ccc;
        margin: 10px auto;
        position: relative;
        .inner-circle {
            display: inline-block;
            width: 70px; 
            height: 70px; 
            border: 1px solid #BE8B66; 
            border-radius: 50%; 
            position: absolute; 
            top: 50%; 
            left: 50%; 
            transform: translate(-50%, -50%);
            img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                object-fit: cover;
            }
        }
    }
    &__owner {
        margin-bottom: 10px; 
        color: #BE8B66; 
        font-size: 2.125rem; 
        font-weight: 400;
        text-align: center;
        @include respond-below(sm) {
            font-size: 1.125rem;
        }
    }
    &__info {
        display: flex;
        justify-content: space-between;
        @include respond-below(lg) {
            display: block;
            p {
                max-width: 100% !important;
                margin-bottom: 15px !important;
            }
        }
        & > p {
            max-width: 345px;
            margin-bottom: 0.75rem;
        }
    }
    &__list {
        position: relative;
        display: flex;
        justify-content: space-between;
        @include respond-below(lg) {
            display: block;
        }
        .sample-img {
            position: absolute;
            left: 50%;
            top: 0;
            transform: translateX(-50%);
            width: 105px;
            height: auto;
            @include respond-below(lg) {
                position: static;
                display: block;
                margin: 20px auto 10px;
                transform: translateX(0);
            }
        }
        .qr-code {
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%);
            width: 54px;
            height: auto;
            @include respond-below(lg) {
                position: static;
                display: block;
                margin: 0 auto;
                transform: translateX(0);
            }
        }
        ul {
            margin: 0;
            padding: 0;
            list-style: none;
            max-width: 345px;
            @include respond-below(lg) {
                max-width: 100%;
            }
            &.left-list {
                .with-bg {
                    background: linear-gradient(to right, #DCDCDC, transparent);
                }
            }
            li {
                padding: 5px 10px;
                &.with-bg {
                    background: linear-gradient(to left, #DCDCDC, transparent);
                }
                &:not(:last-child) {
                    margin-bottom: 5px;
                }
                .second-text {
                    display: inline-block;
                    margin-inline-start: 1.378rem;
                    padding-inline-start: 0 !important;
                    &::before {
                        display: none !important;
                    }
                }
                .small-text {
                    position: relative;
                    padding-inline-start: 10px;
                    margin: 0;
                    &::before {
                        content: '';
                        display: inline-block; 
                        width:4px; 
                        height: 8px; 
                        background: #C6AB84;
                        position: absolute;
                        top: 50%;
                        inset-inline-start: 0;
                        transform: translateY(-50%);
                    }
                }
                .xs-text {
                    position: relative;
                    padding-inline-start: 10px;
                    &::before {
                        content: '';
                        display: inline-block; 
                        width:4px; 
                        height: 4px; 
                        background: #C6AB84;
                        position: absolute;
                        top: 50%;
                        inset-inline-start: 0;
                        transform: translateY(-50%);
                    }
                }
            }
        }
    }
}