.content {
    .typography {
        background: #6264AF;
        padding: 30px;
        margin-bottom: 50px;
        border-radius: 20px;
        color: #fff;
        display: flex;
        align-items: center;
        h1 {
            font-size: 64px;
            margin-inline-end: 50px;
        }
        ul {
            padding: 0;
            list-style: none;
            li {
                margin-bottom: 10px;
            }
        }
    }
    .p-info  {
        display: block;
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 10px;
    }
    &__template {
        &-info {
            font-size: 16px;
            color: #606060;
            display: block;
            margin-top: 20px;
        }
        
        &-title {
            line-height: 1;
            font-size: 40px;
            font-weight: 700;
            margin-bottom: 50px;
            span {
                color: #6264AF
            }
        }
        &-subtitle {
            font-size: 25px;
            font-weight: 700;
            line-height: 1;
            padding-bottom: 30px;
            margin-bottom: 50px;
            border-bottom: 1px solid #D9D9D9;
        }
    }
    &__photo {
        margin-bottom: 30px;
        img {
            width: 100%;
        }
    }
    &__title {
        font-size: 31px;
        color: var(--dark-blue);
        font-weight: 700;
        margin-bottom: 33px;
        @include respond-below(sm) {
            font-size: 25px;
        }
    }
    &__info {
        display: flex;
        padding-bottom: 33px;
        margin-bottom: 30px;
        border-bottom: 1px solid #DDDDDD;
        &.no-border{
            border-bottom: 0;
            padding-bottom: 0;
        }
        & > p {
            margin: 0;
            line-height: 1;
            display: inline-flex;
            align-items: center;
            color: #444444;
            font-size: 14px;
            svg {
                margin-inline-end: 12px;
            }
            &:not(:last-child) {
                margin-inline-end: 22px;
            }
        }
    }
    &__with-sidebar {
        display: flex;
        @include respond-below(lg) {
            flex-wrap: wrap;
        }
        .site-content {
            flex: 0 0 calc(100% - 300px);
            width: calc(100% - 300px);
            padding-inline-start: 30px;
            @include respond-below(lg) {
                flex: 0 0 100%;
                width: 100%;
                order: 1;
            }
        }
    }
    &__sidebar {
        flex: 0 0 300px;
        width: 300px;
        background: #2A346C;
        height: fit-content;
        padding: 30px 20px;
        @include respond-below(lg){
            flex: 0 0 100%;
            width: 100%;
            order: 2;
            margin-bottom: 30px;
        }
        @include respond-below(lg) {
            padding: 30px 10px;
        }
        &-content {
            flex: 0 0 calc(100% - 50px);
            padding-inline-start: 15px;
            p {
                margin-bottom: 0;
                font-size: 14px;
                color: #959FD6;
                margin-top: 10px;
            }
        }
        &-subtitle {
            color: #fff;
            font-size: 16px;
            font-weight: 700;
            line-height: 1;
            margin-bottom: 0;
            
        }
        &-title {
            color: #fff;
            font-size: 22px;
            font-weight: 700;
            line-height: 1;
            margin-bottom: 30px;
            
        }
        &-info-box {
            background: #1B255B;
            padding: 15px;
            display: flex;
            margin-bottom: 40px;
        }
        &-photo {
            width: 50px;
            flex: 0 0 50px;

        }
        &-list {
            padding: 0;
            margin: 0;
            list-style: none;
            li:last-child {
                a {
                    padding-bottom: 0;
                    margin-bottom: 0;
                    border-bottom: 0;
                }
            }
            a {
                display: flex;
                align-items: center;
                text-decoration: none;
                padding-bottom: 15px;
                margin-bottom: 15px;
                border-bottom: 1px solid #4C589C;
                span {
                    display: block;
                }
                .content__sidebar-subtitle {
                    display: block;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    width: 180px;
                    display: block;
                    height: 20px;
                }
            }
        }
    }
}

.site-content {
    margin-bottom: 50px;
    &__frame:not(:last-child) {
        margin-bottom: 30px;
    }
    h1, h2, h3, h4, h5, h6 {
        color: var(--dark-blue);
        margin-bottom: 20px;
    }
    p {
        font-size: 16px;
        font-weight: 700;
        color: #606060;
        line-height: 26px; 
        margin-bottom: 30px;
        @include respond-below(sm) {
            font-size: 14px;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    img {
        max-width: 100%;
        display: block;
        margin: auto;
    }
    ul {
        padding: 0;
        margin: 0;
        list-style: none;
        margin-bottom: 30px;
        &:last-child {
            margin-bottom: 0;
        }
        li {
            position: relative;
            padding-inline-start: 15px;
            &:not(:last-child) {
                margin-bottom: 10px;
            }
            &::before {
                content: '';
                display: block;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background: #606060;
                position: absolute;
                top: 12px;
                inset-inline-start: 0;
            }
        }
    }

}

.rich-content {
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include respond-below(lg) {
            display: block;
        }
    }
    
    &__content-with-img {
        display: flex;
        justify-content: space-between;
        @include respond-below(lg) {
            display: block;
        }
        p {
            max-width: 1000px;
        }
        img {
            max-width: 100%;
            height: fit-content;
            @include respond-below(lg) {
                display: block;
                margin: 0 auto;
            }
        }
    }
    p {
        color: #4E4E4E;
        font-size: 1.375rem;
        font-weight: 300;
        line-height: 40px;
        @include respond-below(sm) {
            font-size: 1.125rem;
            line-height: 30px;
        }
    }
    h6 {
        color: var(--green);
    }
    &.style-2 {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 300px;
    }
}

.download-pdf {
    display: inline-block;
    color: #C6AB84;
    font-size: 1.5rem;
    font-weight: 300;
    text-decoration: none;
    transition: all ease-in-out 0.25s;
    @include respond-below(lg) {
        margin-top: 1rem;
    }
    @include respond-below(sm) {
        font-size: 1rem;
    }
    svg {
        display: inline-block;
        margin-inline-start: 1.125rem;
    }
    &:hover {
        color: #9b8564;
    }
}