.alert-box {
    display: inline-flex;
    background-color: #FFF;
    border-radius: 3px;
    box-shadow: 0 3px 16px rgba(#000, .1);
    &__icon {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 100px;
        height: 95px;
        flex: 0 0 100px;
        @include respond-below(md) {
            flex: 0 0 80px;
            width: 80px;
        }
        @include respond-below(sm) {
            flex: 0 0 60px;
            width: 60px;
        }
    }
    &__message {
        display: flex;
        align-items: center;
        padding: 1rem 3rem;
        margin-bottom: 0;
        @include respond-below(sm) {
            padding: 0 0.75rem;
            font-size: 0.875rem;
        }
    }

    &--danger {
        .alert-box__icon {
            background-color: #FD5151;
            border-radius: 3px;
        }
        .alert-box__message {
            color: #FD5151;
        }
    }
}

.app-alert {
    display: flex;
    gap: 1.125rem;
    padding: 15px 20px;
    align-items: center;
    border-radius: 5px;
    border: 1px solid;
    line-height: 1.3;
    &--info {
        color: #66ccff;
        &::before {
            content: "";
            display: block;
            width: 21px;
            height: 21px;
            flex: 21px 0 0;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("data:image/svg+xml,%0A%3Csvg width='21' height='21' viewBox='0 0 21 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.3755 0.803711C5.02929 0.803711 0.679688 5.15332 0.679688 10.4995C0.679688 15.8458 5.02929 20.1954 10.3755 20.1954C15.7217 20.1954 20.0714 15.8458 20.0714 10.4995C20.0714 5.15332 15.7217 0.803711 10.3755 0.803711ZM11.3078 15.7162H9.44323V13.8516H11.3078V15.7162ZM11.1214 12.737H9.62969L9.35 5.27871H11.401L11.1214 12.737Z' fill='%2366CCFF'/%3E%3C/svg%3E%0A");
        }
    }
}