.profile {
    padding: 2.25rem 0;
    @include respond-below(sm) {
        padding: 0;
    }
    &__wrapper {
        display: flex;
        @include respond-below(lg) {
            display: block;
        }
    }
    &__info_box {
        flex: 0 0 350px;
        width: 350px;
        background: #fff;
        border-radius: 3px;
        box-shadow: 0 5px 83px rgba(#000, 0.16);
        padding: 1.875rem 1.25rem;
        height: fit-content;
        @include respond-below(xxxl) {
            flex: 0 0 300px;
            width: 300px;
        }
        @include respond-below(xl) {
            flex: 0 0 260px;
            width: 260px;
        }
        @include respond-below(lg) {
            width: 100%;
            margin-bottom: 30px;
        }
    }
    &__details_wrapper {
        flex: 0 0 calc(100% - 350px);
        width: calc(100% - 350px);
        padding-inline-start: 0.875rem;
        @include respond-below(xxxl) {
            flex: 0 0 calc(100% - 300px);
            width: calc(100% - 300px);
        }
        @include respond-below(xl) {
            flex: 0 0 calc(100% - 260px);
            width: calc(100% - 260px);
        }
        @include respond-below(lg) {
            width: 100%;
            padding-inline-start: 0;
        }
    }
    &__photo {
        flex: 0 0 90px;
        width: 90px;
        height: 90px;
        border-radius: 50%;
        border: 2px solid #EFEFEF;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto 0.625rem;
        position: relative;
        @include respond-below(sm) {
            width: 70px;
            height: 70px;
            flex: 0 0 70px;
        }
        &.style2 {
            border: 0;
            img {
                width: 100%;
                object-fit: cover;
            }
        }
        img {
            width: 50px;
        }
        svg {
            width: 40px;
            height: auto;
        }
        .change__profile_photo {
            margin: 0;
            cursor: pointer;
            width: 26px;
            height: 26px;
            background: #989FB2;
            border-radius: 50%;
            display: inline-flex;
            justify-content: center;
            align-items: center; 
            position: absolute;
            inset-inline-start: 0;
            bottom: 0;
            input{
                display: none;
            }

        }
    }
    &__name {
        text-align: center;
        color: #1D252D;
        font-style: 1.325rem;
        margin-bottom: 3px;
    }
    &__location {
        text-align: center;
        color: #989FB2;
        font-size: 0.875rem;
        margin: 0;
        svg {
            margin-inline-end: 5px;
        }
    }
    &__public_info {
        list-style: none;
        margin-bottom: 0;
        padding: 0;
        margin-top: 30px;
        background: #fff;
        border-radius: 14px;
        box-shadow: 0 5px 86px rgba(#000, 0.1);
        &>li {
            padding: 30px;
            display: flex;
            &> svg {
                flex: 0 0 20px;
                width: 20px;
                display: block;
            }
            &> div {
                flex: 0 0 calc(100% - 20px);
                width: calc(100% - 20px);
                padding-inline-start: 20px;
            }
            p {
                color: #989FB2;
                font-size: 1rem;
                line-height: 1;
            }
            span {
                display: block;
                color: #1D252D;
                font-size: 1rem;
                font-weight: 700;
            }
            &:not(:last-child){
                border-bottom: 1px solid #D9D6E9;
            }
        }
    }
    &__title_box {
        background: #fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px 20px;
        box-shadow: 0 5px 35px rgba(#000, 0.16);
        border-radius: 3px;
        margin-bottom: 1rem;
        @include respond-below(sm) {
            display: block;
        }
    }
    &__title {
        margin: 0;
        font-weight: 700;
        font-size: 25px;
        color: var(--dark-blue);
        @include respond-below(sm) {
            margin-bottom: 15px;
            font-size: 22px;
        }
    }
    &__details_box {
        background: #fff;
        border-radius: 3px;
        box-shadow: 0 5px 16px rgba(#000, 0.16);
        overflow: hidden;
    }
    &__subtitle {
        background: #fff;
        padding: 15px 60px;
        color: #989FB2;
        font-size: 22px;
        font-weight: 700;
        box-shadow: 0 5px 30px rgba(#000, 0.05);
        margin-bottom: 0;
        @include respond-below(xxxl) {
            padding: 15px 30px;
        }
        @include respond-below(sm) {
            font-size: 18px;
        }
    }
    &__entities-row {
        display: grid;
        grid-template-columns: repeat(3, minmax(0, 1fr));
        padding: 2rem 4rem;
        gap: 1rem;
        @include respond-below(xxxl) {
            padding: 2rem;
            grid-template-columns: repeat(2, minmax(0, 1fr));
        }
        @include respond-below(xl) {
            grid-template-columns: repeat(1, minmax(0, 1fr));
        }
    }
    &__entities {
        padding: 1.5rem 1.125rem;
        border-bottom: 1px solid rgba(#707070, 0.15);
        box-shadow: 0 5px 30px rgba(#000, 0.07);
        &.no-border {
            border-bottom: 0;
        }
    }
    &__sub_entities_box  {
        margin-top: 55px;
        background: #fff;
        border-radius: 5px;
        padding: 3rem 1rem 2rem;
        position: relative;
        box-shadow: 0 5px 30px rgba(#000, 0.07);
        @include respond-below(md) {
            margin-inline-start: 0;
        }
        @include respond-below(sm) {
            margin-top: 35px;
        }
        .sub_entities_title  {
            margin: 0;
            background: #989FB2;
            padding: 12px 30px;
            color: #fff;
            font-size: 14px;
            font-weight: 700;
            border-radius: 33px;
            position: absolute;
            inset-inline-start: 20px;
            top: -25px;
            @include respond-below(sm) {
                inset-inline-start: 0;
                width: 100%;
                text-align: center;
            }
        }
        & > .entity__informations:not(:last-child) {
            padding-bottom: 30px;
            margin-bottom: 30px;
            border-bottom: 1px solid rgba(#707070, 0.15);
        }
    }
    &__data {
        padding: 50px 60px 30px;
        border-bottom: 1px solid rgba(#707070, 0.15);
        @include respond-below(xxxl) {
            padding: 50px 30px 30px;
        }
        @include respond-below(xxl) {
            padding-bottom: 25px;
        }
        @include respond-below(sm) {
            padding: 40px 30px 20px;
        }
        &.no-border {
            border-bottom: 0;
        }
        &.flex-style {
            display: flex;
            @include respond-below(xxl) {
                flex-wrap: wrap;
            }
            @include respond-below(md) {
                display: block;
            }
            .information__box {
                flex: 0 0 25%;
                @include respond-below(xxl) {
                    flex: 0 0 33.333%;
                    margin-bottom: 10px;
                }
                @include respond-below(xl) {
                    flex: 0 0 50%;
                }
            }
        }
    }
    &__entities-info {
        display: flex;
        gap: 0.5rem;
        @include respond-below(sm) {
            flex-direction: column;
        }
    }
}

.entity {
    &__informations {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    &__photo {
        flex: 0 0 60px;
        width: 60px;
        height: 60px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 15px;
        img {
            max-height: 100%;
            object-fit: contain;
        }
    }
    &__information__wrapper {
        flex: 0 0 calc(100% - 60px);
    }
    &__name {
        color: #1D252D;
        font-weight: 700;
        font-size: 18px;
        margin-bottom: 15px;
    }
    &__status {
        border-radius: 3px;
        padding: 5px 20px;
        margin: 0;
        font-size: 14px;
        height: fit-content;
        display: inline-block;
        @include respond-below(sm) {
            display: inline-block;
            margin-bottom: 15px;
        }
        &.inprogress {
            color: #E5A430;
            background: rgba(#E5A430, 0.13);
        }
        &.completed {
            color: var(--green);
            background: rgba(#4DBD95, 0.13);
        }
        &.uncompleted {
            color: #1BCED8;
            background: rgba(#1BCED8, 0.13);
        }
    }
    &__info_list {
        padding: 0;
        margin: 0;
        list-style: none;
        display: flex;
        height: fit-content;
        &> li {
            color: #989FB2;
            font-size: 14px;
            svg {
                margin-inline-end: 10px;
            }
            &:not(:last-child) {
                padding-inline-end: 20px;
                margin-inline-end: 20px;
                border-inline-end: 1px solid #EAEAEA;
            }
        }
    }
}

.information {
    &__icon {
        background: #FAFAFA;
        flex: 0 0 60px;
        width: 60px;
        height: 60px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        border-radius: 3px;
    }
    &__data {
        flex: 0 0 calc(100% - 60px);
        padding-inline-start: 20px;
        .data-label {
            color: #989FB2;
            font-size: 14px;
            margin: 0;
        }
        .data-info {
            color: #1D252D;
            font-size: 20px;
            margin: 0;
        }
    }
    &__box {
        display: flex;
        flex-grow: 1;
        
        &--small {
            .information__icon {
                width: 30px;
                height: 30px;
                flex: 30px 0 0;
                svg {
                    width: auto;
                    height: 14px;
                    max-width: 100%;
                }
            }
            .information__data {
                flex: 0 0 calc(100% - 30px);
                padding-inline-start: 10px;
            }
            .data-info {
                font-size: 18px;
            }
        }
    }
}

.entity-landscape {
    display: flex;
    @include respond-below(lg) {
        display: block;
    }
    &__profile{
        flex: 0 0 540px;
        width: 540px;
        background: #5758A1;
        padding: 100px 70px;
        text-align: center;
        @include respond-below(xxxl) {
            flex: 0 0 370px;
            width: 370px;
            padding: 100px 30px;
        }
        @include respond-below(lg) {
            width: 100%;
            padding: 50px 30px;
        }
    }
    .landscape-welcome {
        font-size: 2.5rem;
        color: #CBCCFF;
        font-weight: 700;
        @include respond-below(sm) {
            font-size: 1.875rem;
        }
    }
    .profile__photo {
        width: 120px;
        height: 120px;
        overflow: hidden;
        @include respond-below(sm) {
            width: 90px;
            height: 90px;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 50%;
        }
    }
    &__name {
        color: #fff;
        font-size: 2.125rem;
        margin-bottom: 3.125rem;
        @include respond-below(xxxl) {
            font-size: 1.75rem;
        }
        @include respond-below(sm) {
            font-size: 1.25rem;
            margin-bottom: 2rem;
        }
    }
    &__profile-info {
        font-size: 1.25rem;
        color: #DEDEFF;
        margin-bottom: 3.125rem;
        @include respond-below(sm) {
            font-size: 1rem;
            margin-bottom: 2rem;
        }
    }
    .main-box__body {
        flex-grow: 1;
        padding-inline: 2.75rem;
        @include respond-below(xxxl) {
            padding-inline: 2rem;
        }
    }
}