.table {
    vertical-align: middle;
    color: #1D252D;
    &.table-striped {
        --bs-table-striped-bg: #F8F8F8;
    }
    th {
        background-color: var(--dark-blue);
        color: #FFF;
        white-space: nowrap;
    }
    th, td {
        padding: 1rem 1.5rem;
    }
    &>:not(caption)>*>* {
        border: unset;
    }
    &__entity-name {
        display: flex;
        align-items: center;
        gap: 1rem;
        span {
            white-space: nowrap;
            display: block;
        }
        img {
            display: inline-block;
            flex: 40px 0 0;
            width: 40px;
            height: auto;
        }
    }
    &__status {
        line-height: 1;
        display: inline-block;
        padding: .3rem .8rem .65rem;
        border-radius: 3px;
        white-space: nowrap;
        min-width: 125px;
        text-align: center;
        &--incomplete {
            color: #F16739;
            background-color: rgba(#F16739, .1);
        }
        &--completed {
            color: #4DBD95;
            background-color: rgba(#4DBD95, .1);
        }
        &--inprogress {
            background-color: rgba(#73C8F1, .1);
            color: #1BCED8;
        }
        &--review {
            background: rgba(#FCBD4C, 0.1);
            color: var(--orange);
        }
        &--reject {
            color: #E81111;
            background: rgba(#E81111, 0.2);
        }
    }
    &__actions {
        width: 0;
        .button {
            min-width: 150px;
        }
        .table-button-edit {
            padding: 11px 13px;
            min-width: auto;
            svg path {
                transition: all ease-in-out 0.25s;

            }
            &:hover {
                svg path {
                    fill: #fff;;
                }
            }
        }

    }
    &__filter {
        margin-bottom: 2rem;
        display: flex;
        .form__search {
            flex-grow: 1;
            max-width: 400px;
        }
    }
}

.table-responsive {
    border-radius: 3px;
    &--with-shadow {
        box-shadow: 0 5px 15px rgba(#000, .16);
        .table {
            margin-bottom: 0;
        }
    }
}