.main-box {
    background-color: #FFF;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - 240px);
    @include lightMode() {
        box-shadow: 0 10px 20px rgba(#263570, .16);
    }
    @include respond-below(sm) {
        padding: 1rem;
    }
    @include respond-below(lg) {
        min-height: auto;
    }
    &__header {
        border-bottom: 1px solid #EAEAEA;
        padding-bottom: 1.5rem;
        &--with-button {
            display: flex;
            justify-content: space-between;
            align-items: center;
            @include respond-below(md) {
                flex-direction: column;
                gap: 1.5rem;
            }
        }
        &.no-border {
            border-bottom: 0;
        }
        .breadcrumb {
            margin: 0;
            ul {
                margin-top: 1.875rem;
                padding: 0;
                margin-bottom: 0;
                list-style: none;
                display: flex;
                li {
                    a, span {
                        color: #989FB2;
                        font-size: 13px;
                        display: block;
                    }
                    a {
                        text-decoration: none;
                        padding-inline-end: 10px;
                        margin-inline-end: 10px;
                        position: relative;
                        transition: all ease-in-out 0.25s;
                        @include respond-below(sm) {
                            padding-inline-end: 5px;
                            margin-inline-end: 5px;
                        }
                        &:hover {
                            color: #575b66;
                        }
                        &::after {
                            content: '/';
                            color: #989FB2;
                            font-size: 13px;
                            position: absolute;
                            inset-inline-end: 0;
                            top: 50%;
                            transform: translateY(-50%);
                            
                        }
                    }
                }
            }
        }
    }
    &__main-title {
        color: var(--dark-blue);
        font-size: 2rem;
        font-weight: 600; 
        margin-bottom: 0;
        @include respond-below(md) {
            font-size: 1.75rem;
        }
        @include respond-below(sm) {
            font-size: 1.25rem;
        }
    }
    &__sub-title {
        color: var(--green);
        font-size: 1rem;
        font-weight: 600; 
        margin-bottom: 0;
        margin-top: .75rem;
        &--gray {
            color: #989FB2;
            font-weight: 500;
        }
    }
    &__body {
        padding-block: 3rem;
        @include respond-below(md) {
            padding-block: 2rem;
        }
    }
    &__footer {
        margin-top: auto;
        border-top: 1px solid #EAEAEA;
        padding-top: 1.5rem;
        display: flex;
        justify-content: flex-end;
        gap: 2rem;
        @include respond-below(md) {
            justify-content: center;
            gap: 1rem;
        }
        .button {
            min-width: 150px;
            @include respond-below(sm) {
                min-width: unset;
            }
        }
    }
    &.style2 {
        padding: 0;
        min-height: auto;
    }
}
.secondary-box {
    position: relative;
    z-index: 1;
    &__head {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1.5rem;
        margin-bottom: 1.5rem;
        h2 {
            color: var(--primary);
            font-size: 2rem;
            font-weight: 600;
            margin-top: 2.125rem;
            margin-bottom: 0;
            @include respond-below(sm) {
                font-size: 1.5rem;
            }
            & + p {
                font-weight: 400;
                color: rgba(42, 52, 108, 0.6);
                margin-bottom: 0;
                text-align: center;
            }
        }
        img {
            padding: 2rem 0rem;
            z-index: 1;
            object-fit: contain;
            max-width: 100%;
        }
    }
    &::before {
        content: '';
        background: url('../images/bg-shapes1.png') no-repeat;
        max-width: 260px;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        pointer-events: none;
        opacity: 0.3;
        z-index: -1;
    }
    &::after {
        content: '';
        background: url('../images/bg-shapes2.png') no-repeat;
        max-width: 700px;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        pointer-events: none;
        opacity: 0.3;
        z-index: -1;
    }
}
.user-profile-info {
    display: flex;
    align-items: center;
    gap: 1rem;
    .profile__photo {
        margin-bottom: 0;
    }
}