/** font faces */
@font-face {
    font-family: "HelveticaNeueLTArabic";
    src: url("../fonts/HelveticaNeueLTArabic-Light.ttf");
    font-weight: 300;
}
@font-face {
    font-family: "HelveticaNeueLTArabic";
    src: url("../fonts/HelveticaNeueLTArabic-Roman.ttf");
    font-weight: 400;
}
@font-face {
    font-family: "HelveticaNeueLTArabic";
    src: url("../fonts/HelveticaNeueLTArabic-Bold.ttf");
    font-weight: 600;
}

/** font faces */
@include respond-above(xxxl) {
    .container {
        max-width: 1650px;
    }
}

a {
    cursor: pointer;
}

body {
    color: #1D252D;
    font-family: 'HelveticaNeueLTArabic', 'sans-serif';
    position: relative;
    background-image: linear-gradient(#263570, #242F5C);
    &.light-mode-active {
        background-image: none;
    }

    & > .modal-backdrop {
        display: none;
    }
}

.dropdown-menu {
    color: var(--dark-blue);
    padding: 0;
    border: 0;
    box-shadow: 0 4px 30px rgba(#000, .16);
    border-radius: 5px;
    overflow: hidden;
    li:not(:last-child) a {
        border-bottom: 1px solid #DDE0E2;
    }
}
.dropdown-item {
    text-align: start;
    color: var(--dark-blue);
    padding: .6rem 1rem .6rem 2rem;
    transition: all .25s ease-in-out;
    &:active {
        background-color: var(--dark-blue);
    }
}

.site-main {
    min-height: 100vh;
    position: relative;
    padding-top: 100px;
    padding-bottom: 140px;
    @include respond-below(lg) {
        padding-bottom: 260px;
    }
    @include respond-below(sm) {
        padding-bottom: 300px;
    }
    
    .main-bg {
        background: url("../images/main-bg.jpg") center top no-repeat;
        background-size: cover;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-attachment: fixed;
        z-index: -1;
        pointer-events: none;
        @include lightMode() {
            background: #F7F7F7;
        }
        &:before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            bottom: 0;
            left: 0;
            background-image: linear-gradient(transparent,transparent, rgb(34 32 35 / 32%),#222023);
        }
    }
    
}

.danger-info {
    display: flex;
    color: var(--red);
    gap: 1rem;
}

.gray-box {
    background-color: #F3F3F3;
    padding: 1rem 1.5rem;
    border-radius: 3px;
}

.green-bg {
    background-color: var(--green);
}

.light-blue-bg {
    background-color: var(--light-blue);
}

.green-text {
    color: var(--green) !important;
}


.error-box  {
    border: 1px solid #E2E2E2;
    max-width: 700px;
    margin-inline: auto;
    margin-bottom: 3rem;
    border-radius: 10px;
    padding: 3rem 1rem;
    h3 {
        font-size: 2.75rem;
        font-weight: 600;
        color: #F16739;
        margin-bottom: 2rem;
        svg {
            display: inline-block;
            margin-inline-end: 1rem;
        }
    }
    p {
        font-size: 1.5rem;
        color: var(--dark-blue);
    }
}


.site-loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99999;
    background-color: rgba(39, 52, 112, .1);
    backdrop-filter: blur(35px);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    &.active {
        opacity: 1;
        visibility: visible;
        .animate-1 {
            animation: opacityAnimate 1s ease-in-out infinite alternate;
        }
        .animate-2 {
            animation: opacityAnimate 1s ease-in-out 1.25s infinite alternate;
        }
        .animate-3 {
            animation: opacityAnimate 1s ease-in-out 1.5s infinite alternate;
    
        }
        .animate-4 {
            animation: opacityAnimate 1s ease-in-out 1.75s infinite alternate;
        }
    }
}

@keyframes opacityAnimate {
    0%, 10% {
        opacity: 1;
    }
    90%, 100% {
        opacity: 0;
    }
}

