// general paragh style

p {
    font-size: 1rem;
    &.small-1 {
        font-size: 14px;
    }
    &.small-2 {
        font-size: 12px; 
        font-weight: bold;
    }
}

/* Start Slide Button */
.slide-button-wrapper {
    display: flex;
    justify-content: center;
    gap: 1.25rem
}

.slide-button {
    font-size: 0.75rem;
    color: var(--primary-hover);
    position: relative;
    z-index: 1;
    user-select: none;
    transition: all ease-in-out 0.25s;
    &:hover {
        color: var(--primary)
    }
    &-light {
        color: #fff;
        &:hover {
            color: #E2E2E2
        }
    }
    &::before {
        content: '';
        display: block;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background: var(--primary-hover);
        opacity: 0.2;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: -1;
    }
    &--next {
        i {
            margin-inline-end: 0.625rem;
            
        }
        &::before {
            inset-inline-start: -20px;
        }
    }
    &--prev {
        i {
            margin-inline-start: 0.625rem;
        }
        &::before {
            inset-inline-end: -20px;
        }
    }
}
/* End Slide Button */

.steps-form .gray-box {
    .steps-form__attachments {
        @include respond-below(xl) {
            display: flex !important;
            flex-direction: column;
            gap: 1rem;
            align-items: stretch;
            .steps-form__attachments__content {
                margin-top: 0 !important;
            }
            .steps-form__attachments__warning {
                padding-top: 0;
            }
        }
    }
}

.eligibility-questions {
    .eligibility-questions__title + .text-center {
        .button {
            @include respond-below(sm) {
                margin: 0 !important;
                width: 100%;
                &:nth-child(2) {
                    margin-top: 5px !important;
                }
            }
        }
    }
}

html[dir="rtl"] .dynamic-text-align{text-align: right;}
html[dir="ltr"] .dynamic-text-align{text-align: left;}
html[dir="rtl"] .dynamic-reversed-text-align{text-align: left;}
html[dir="ltr"] .dynamic-reversed-text-align{text-align: right;}
