.loader-parent {
    position: relative;
    
    
}
.loader {
    &::before {
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-image: linear-gradient(45deg, rgba(#6459a7, .4) 0%, rgba(#263570, .4) 100%);
        backdrop-filter: blur(10px);
        z-index: 9;
        visibility: hidden;
        opacity: 0;
        transition: all .25s ease-in-out;
    }
    &::after {
        content: "";
        @include center('both');
        display: inline-block;
        width: 40px;
        height: 40px;
        border: 5px solid #263570;
        border-radius: 50%;
        z-index: 10;
        filter: drop-shadow(0 10px 30px rgba(#000, .1));
        border-left-color: transparent;
        visibility: hidden;
        opacity: 0;
        transition: all .25s ease-in-out;
        animation: rotateLoader 1s linear infinite;
        animation-play-state: paused;
    }
    &--active {
        &::before {
            visibility: visible;
            opacity: 1;
        }
        &::after {
           visibility: visible;
            opacity: 1;
            animation-play-state: running;
        }
    }
}



@keyframes rotateLoader {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}