.choose-login-type {
    display: flex;
    gap: 3rem;
    padding: 1rem;
    @include respond-below(sm) {
        display: block;
    }
    &__item {
        flex-grow: 1;
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 5px;
        box-shadow: 0 10px 25px rgba(#000, .14);
        padding: 2rem 1rem;
        background-color: #FFF;
        transition: background-color .25s ease-in-out;
        color: var(--light-blue);
        text-decoration: none;
        width: 50%;
        text-align: center;
        @include respond-below(sm) {
            display: flex;
            width: 100%;
        }
        p {
            margin-bottom: 0;
            margin-top: .5rem;
            font-weight: 600;
            font-size: 1.125rem;
        }
        &:hover {
            background-color: #F6F6F6;
            color: var(--light-blue);
        }
    }
    &__or {
        display: inline-flex;
        align-items: center;
        color: #989FB2;
        font-weight: 600;
        @include respond-below(sm) {
            display: block;
            text-align: center;
            margin-block: 1rem;
        }
    }
}