.home {
  // min-height: 100vh;
  // position: relative;
  padding-top: 60px;
  // overflow: hidden;
  // background-image: linear-gradient(#263570, #242F5C, #12100C);
  @include lightMode() {
    background-image: none;
  }
  
  @include respond-below(lg) {
    display: flex;
    align-items: center;
  }
  @include respond-below(xl) {
    padding-top: 20px;
  }
  &:before {
    content: "";
    display: inline-block;
    position: absolute;
    width: 50%;
    height: 100%;
    top: 0;
    inset-inline-start: 50%;
    background: url('../images/hero-bg.svg') center center no-repeat;
    background-size: contain;
    padding-inline-end: 120px;
    background-origin: content-box;
    pointer-events: none;
    @include lightMode() {
      opacity: .3;
    }
    @include respond-below(xl) {
      padding-inline-end: 40px;
    }
    @include respond-below(lg) {
      inset-inline-start: 0;
      padding-inline-end: 0;
      width: 100%;
      opacity: .1 !important;
    }
    @include respond-below(sm) {
      background-position: center 80px;
    }
  }
  &:after {
    content: "";
    display: inline-block;
    position: absolute;
    width: 50%;
    height: 100%;
    top: 0;
    inset-inline-end: 50%;
    background: url('../images/hero-bg-2.svg') no-repeat;
    background-size: contain;
    padding-inline-end: 10%;
    background-origin: content-box;
    pointer-events: none;
    opacity: .5;
    @include lightMode() {
      opacity: .2;
    }
    @include rtl() {
      background-position: bottom right;
    }
    @include ltr() {
      background-position: bottom left;
    }
    @include respond-below(lg) {
      content: none;
    }
  }
  &__content {
    width: 50%;
    @include respond-below(lg) {
      width: 100%;
      text-align: center;
    }
    h1 {
      font-size: 3rem;
      font-weight: 600;
      color: var(--light-blue);
      position: relative;
      z-index: 1;
      @include lightMode() {
        color: var(--dark-blue);
      }
      @include respond-below (xxxl) {
        font-size: 2.5rem;
      }
      @include respond-below (xxl) {
        font-size: 2.25rem;
      }
      @include respond-below (lg) {
        font-size: 2rem;
      }
      @include respond-below (sm) {
        font-size: 1.75rem;
      }
    }
  }
}

.register-type {
  margin-top: 100px;
  @include respond-below (xxxl) {
    margin-top: 60px;
  }
  @include respond-below (sm) {
    margin-top: 40px;
  }
  &__title {
    color: #FFF;
    font-weight: 300;
    font-size: 1.875rem;
    margin-bottom: 2.25rem;
    position: relative;
    z-index: 1;
    @include lightMode() {
      color: var(--dark-blue);
    }
    @include respond-below (xxxl) {
      font-size: 1.75rem;
    }
    @include respond-below (xxl) {
      font-size: 1.5rem;
      margin-bottom: 1.75rem;
    }
    @include respond-below (lg) {
      font-size: 1.25rem;
      margin-bottom: 1.5rem;
    }
    @include respond-below (sm) {
      font-size: 1.125rem;
    }
  }
  &__list {
    display: flex;
    gap: 2.5rem;
    @include respond-below(lg) {
      justify-content: center;
    }
    @include respond-below(sm) {
      gap: .5rem;
      justify-content: center;
    }
  }
  &__item {
    text-decoration: none;
    border: 2px solid rgba(#66CCFF, .6);
    min-width: 210px;
    padding: 2rem 1.75rem;
    height: 100%;
    min-height: 190px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    transition: background-color .25s ease-in-out;
    backdrop-filter: blur(20px);
    position: relative;
    z-index: 1;
    @include lightMode() {
      border: 2px solid rgba(#263570, .6);
      path {
        fill: var(--dark-blue);
        transition: fill .25s ease-in-out;
      }
      span {
        color: var(--dark-blue);
        transition: color .25s ease-in-out;
      }
      &:hover {
        background-color: var(--dark-blue);
        span {
          color: #FFF;
        }
        path {
          fill: #FFF;
        }
      }
    }
    @include respond-below (xxl) {
      padding: 1.75rem 1.5rem;
      min-width: 190px;
      min-height: 170px;
    }
    @include respond-below (lg) {
      align-items: center;
      min-height: 150px;
    }
    @include respond-below(sm) {
      width: 50%;
      min-width: unset;
      padding: 1.5rem 1rem;
      min-height: 130px;
      max-width: 190px;
    }
    svg {
      height: 55px;
      width: auto;
      @include respond-below (xxl) {
        height: 40px;
      }
      @include respond-below (lg) {
        height: 35px;
      }
    }
    span {
      display: block;
      color: #FFF;
      font-size: 1.25rem;
      font-weight: 600;
      margin-top: auto;
      @include respond-below (xxl) {
        font-size: 1.125rem;
      }
      @include respond-below (sm) {
        font-size: .875rem;
      }
    }
    &:hover {
      background-color: rgba(#FFF, .1);
    }
  }
  &__dropdown {
    position: relative;
    @include respond-below(lg) {
      position: static;
    }
    @include respond-below(sm) {
      width: 50%;
      max-width: 190px;
      .register-type__item {
        width: 100%;
        max-width: unset;
      }
    }
    .dropdown-menu {
      top: 30px;
      inset-inline-start: 0;
      left: auto;
      min-width: 350px;
      margin: 0;
      border-radius: 0;
      min-height: 190px;
      text-align: start;
      background-color: rgba(150, 140, 209, .3);
      border: 2px solid rgba(#686C9B, .25);
      backdrop-filter: blur(30px) brightness(0.75);
      padding: 1.5rem 1rem;
      display: block;
      visibility: hidden;
      opacity: 0;
      transition: all .25s ease-in-out;
      @include lightMode() {
        background-color: rgba(#263570, .7);
        border: 2px solid #263570;
      }
      @include respond-below(lg) {
        position: fixed;
        box-shadow: 0 0 0 2000px rgba(#000, .8);
        z-index: 9999;
        left: 50%;
        transform: translate(-50%, -50%);
        top: calc(50% + 30px);
        inset-inline-start: auto;
        min-width: unset;
        width: min(350px, 90%);
      }
      &.show {
        visibility: visible;
        opacity: 1;
        top: 0;
        @include respond-below(lg) {
          top: 50%;
        }
      }
      
    }
    .dropdown-item {
      color: #FFF;
      transition: background-color .25s ease-in-out;
      padding: 1rem 1.25rem;
      padding-inline-end: 3rem;
      display: inline-flex;
      align-items: center;
      gap: 2rem;
      font-size: 1.25rem;
      position: relative;
      svg {
        width: 35px;
        height: auto;
      }

      @include respond-below(sm) {
        padding: 1rem 1.25rem;
        font-size: 1rem;
        svg {
          width: 25px;
        }
      }
      &:hover, 
      &:active {
        background-color: rgba(#FFF, .1);
      }

      &:not(:last-child) {
        &:before {
          content: "";
          position: absolute;
          width: 100%;
          height: 1px;
          bottom: 0;
          inset-inline-start: 0;
          background-image: linear-gradient(90deg, transparent, #FFF 150%);
        }
      }
    }
  }

}