.button {
  display: inline-flex;
  align-items: center;
  padding: .6rem 1.5rem .9rem;
  border-radius: 3px;
  border: 1px solid transparent;
  text-decoration: none;
  transition: border .25s ease-in-out,
              background-color .25s ease-in-out,
              color .25s ease-in-out;
  text-align: center;
  justify-content: center;
  font-weight: 600;
  gap: 1rem;
  background-color: transparent;
  white-space: nowrap;
  svg {
    max-height: 24px;
    width: auto;
  }
  &--dark-blue {
    background-color: var(--dark-blue);
    color: #FFF;
    &:hover {
      color: #FFF;
      background-color: #1c264e;
    }

    &[disabled] {
      background-color: #989FB2 !important;
      cursor: auto;
    }
  }
  &--dark-blue-outline {
    border-color: var(--dark-blue);
    color: var(--dark-blue);
    &:hover {
      color: #FFF;
      background-color: var(--dark-blue);
    }
  }
  &--light-blue {
    background-color: var(--light-blue);
    color: #FFF;
    &:hover {
      color: #FFF;
      background-color: var(--light-blue-2);
    }
  }
  &--light-blue-outline {
    border-color: var(--light-blue);
    color: var(--light-blue);
    &:hover {
      color: #FFF;
      background-color: var(--light-blue);
    }
    &[disabled] {
      background-color: #E3E4E9 !important;
      border-color: #E3E4E9 !important;
      cursor: auto;
      color: #C9C9C9 !important;
    }
  }
  &--gray {
    background-color: #F5F5F5;
    color: #303B6F;
    border: 1px solid rgba(#5758A1, 0.06);
    &:hover {
      color: #303B6F;
      background-color: #e6e4e4;
    }
  }
  &--light-purple {
    background-color: #EEEEF4;
    border-color: #B7B7D6;
    color: #5758a1;
    &:hover {
      color: #5758a1;
      background-color: #dbdbe7;
    }
  }
  &--green {
    background-color: var(--green);
    color: #FFF;
    &:hover {
      color: #FFF;
      background-color: #3e9f7c;
    }
  }
  &--light-green {
    background-color: var(--light-green);
    color: #FFF;
    &:hover {
      color: #FFF;
      background-color: #8BB439;
    }
  }
  &--orange {
    background-color: var(--orange);
    color: #FFF;
    &:hover {
      color: #FFF;
      background-color: #eab045;
    }
  }
  &--dark-orange {
    background-color: var(--dark-orange);
    color: #FFF;
    &:hover {
      color: #FFF;
      background-color: #d46921;
    }
  }
  &--orange-outline {
    border-color: var(--orange);
    color: var(--orange);
    &:hover {
      color: #FFF;
      background-color: var(--orange);
    }
  }
  &--red-outline {
    border-color: var(--red);
    color: var(--red);
    &:hover {
      color: #FFF;
      background-color: var(--red);
    }
  }
  &--purple {
    background: var(--primary-hover);
    color: #fff;
    border-radius: 10px;
    &:hover {
      background: var(--primary);
      color: #fff;
    }
  }
  &--full-width {
    width: 100%;
  }
  &--blocked {
    display: block;
    max-width: fit-content;
    padding: 0.125rem 0rem;
    margin-top: 0.25rem;
    color:  var(--dark-blue);
    &:hover {
      color: #1c264e;
    }
  }
  &--small {
    padding: .3rem 1.5rem .6rem;
    svg {
      position: relative;
      top: 2px;
    }
  }
  &__primary {
    background: var(--primary);
    color: #fff;
    svg path {
      fill :#fff;
    }
    &:hover {
      background: var(--primary-hover);
    }
  }
  &__primary-outline {
    color: var(--primary);
    border-color: var(--primary);
    svg path {
      fill: var(--primary);
       transition: all ease-in-out 0.25s;
    }
    &:hover {
      color: var(--primary-hover);
      border-color: var(--primary-hover);
      svg path {
        fill: var(--primary-hover)
      }
    }
  }
  &__secondary {
    background: var(--secondary);
    color: #fff;
    svg path {
      fill :#fff;
    }
    &:hover {
      background: var(--secondary-hover);
    }
  }
  &__secondary-outline {
    color: var(--secondary);
    border-color: var(--secondary);
    svg path {
      fill: var(--secondary);
      transition: all ease-in-out 0.25s;
    }
    &:hover {
      color: var(--secondary-hover);
      border-color: var(--secondary-hover);
      svg path {
        fill: var(--secondary-hover);
      }
    }
  }
  &__success {
    background: var(--green);
    color: #fff;
    svg path {
      fill :#fff;
    }
    &:hover {
      background: #3e9f7c;
    }
  }
  &__success-outline {
    color: var(--green);
    border-color: var(--green);
    svg path {
      fill: var(--green);
      transition: all ease-in-out 0.25s;
    }
    &:hover {
      color: #3e9f7c;
      border-color: #3e9f7c;
      svg path {
        fill: #3e9f7c;
      }
    }
  }
  &__warning {
    background: var(--orange);
    color: #fff;
    svg path {
      fill :#fff;
    }
    &:hover{
      background: #eab045;;
    }
  }
  &__warning-outline {
    color: var(--orange);
    border-color: var(--orange);
    svg path {
      fill: var(--orange);
      transition: all ease-in-out 0.25s;
    }
    &:hover {
      color: #eab045;
      border-color: #eab045;
      svg path {
        fill: #eab045;;
      }
    }
  }
  &__danger {
    background: var(--danger);
    color: #fff;
    svg path {
      fill :#fff;
    }
    &:hover {
      background: var(--danger-hover);
    }
  }
  &__danger-outline {
    color: var(--danger);
    border-color: var(--danger);
    svg path {
      fill: var(--danger);
      transition: all ease-in-out 0.25s;
    }
    &:hover {
      color: var(--danger-hover);
      border-color: var(--danger-hover);
      svg path {
        fill: var(--danger-hover)
      }
    }
  }
  &__disabled {
    background: var(--sliver);
    color: var(--lightSliver);
    pointer-events: none;
    svg path {
      fill: var(--lightSliver)
    }
  }
