      width: 20px;
      height: 20px;
      display: inline-block;
      vertical-align: middle;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }
}
.primary-link {
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  transition: all .25s ease-in-out;    
  justify-content: center;
  font-weight: 600;
  gap: 1rem;
  background-color: transparent;
  white-space: nowrap;
  color: var(--primary);
  svg path{
    fill: var(--primary);
    transition: all ease-in-out 0.25s;
  }
  &:hover {
    color: var(--primary-hover);
    svg path {
      fill: var(--primary-hover);
    }
  }
}

.status {
  display: inline-block;
  padding: .3rem .8rem .65rem;
  border-radius: 3px;
  white-space: nowrap;
  min-width: 125px;
  text-align: center;
  &__general {
    color: var(--secondary);
    background: rgba(#73c8f1, 0.3);
  }
  &__success {
    color: var(--green);
    background: rgba(#4DBD95, 0.3);
  }
  &__warning {
    color: var(--orange);
    background: rgba(#FCBD4C, 0.3);
  }
  &__danger {
    color: var(--danger);
    background: rgba(#f16739, 0.3);
  }
}