.blue-banner {
    background: linear-gradient(180deg, #263570 0%, #12100C 100%);
    padding: 104px 50px;
    position: relative;
    margin-bottom: 1.25rem;
    z-index: 1;
    @include respond-below(sm) {
        padding: 100px 20px;
    }
    &__title {
        color: #fff;
        text-align: center; 
        font-size: 1.875rem;
        margin-bottom: 1.875rem;
    }
    p {
        text-align: center;
        color: #fff;
        margin-bottom: 2.5rem;
    }
    &__search-container {
        max-width: 500px;
        margin: 0 auto;
        position: relative;
        .search__submit  {
            inset-inline-end: 5px;
            @include respond-below(sm) {
                position: static;
                transform: translateY(0);
                margin-top: 1rem;
                display: block;
            }
        }
        .search__input {
            @include respond-below(sm) {
                padding-inline-end: 1.25rem;
            }
        }
    }
    &::before {
        content: '';
        background: url('../images/bg-shapes1.png') no-repeat;
        max-width: 260px;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        pointer-events: none;
        z-index: -1;
    }
    &::after {
        content: '';
        background: url('../images/bg-shapes2.png') no-repeat;
        max-width: 700px;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        pointer-events: none;
        z-index: -1;
    }
}

.procedural-box  {
    background: #fff;
    box-shadow: 0px 3px 25px rgba(#000, 0.05);
    padding: 1.875rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    height: 100%;
    &__title {
        color: var(--dark-blue);
        font-size: 1rem;
        margin-bottom: 1.875rem;
    }
}