.custom-accordion {
    box-shadow: 0px 3px 25px rgba(0, 0, 0, 0.03);
    border: 1px solid #EFF1FA;
    &:not(:last-of-type) {
        margin-bottom: 0.875rem;
    }
    &__question {
        &--disabled {
            .custom-accordion__question-title {
                opacity: .5;
            }
        }
    }
    &__question-title {
        color: #444;
        font-weight: 600;
        font-size: 15px;
        margin-bottom: 1.375rem;
    }
    &__answer-radio-wrapper {
        display: flex;
        gap: 70px;
        @include respond-below(sm) {
            gap: 10px;
            flex-direction: column;
        }
        .form-check {
            @include respond-above(md) {
                min-width: 150px;
            }
        }
    }
    &__status {
        margin: 0;
        padding: 0.5rem;
        font-size: 0.875rem;
        text-align: center;
        display: inline-flex;
        gap: .5rem;
        white-space: nowrap;
        
        @include respond-above(md) {
            margin-top: -.5rem;
            margin-bottom: -.5rem;
        }
        
        &.success-status {
            border: 1px solid #4CBC94;
            border-radius: 30px;
            background: #E2F2E3;
            color: var(--green);
        }
        &.warning-status {
            background: #FBEACC;
            border: 1px solid #ECA320;
            border-radius: 30px;
            color: #ECA320;
        }
    }
    &__header {
        position: relative;
        .accordion-button {
            color: var(--primary);
            font-size: 1rem;
            font-weight: 600;
            text-align: unset;
            display: flex;
            justify-content: space-between;
            align-items: center;  
            border: none;
            padding: 1.3rem 1.875rem;
            box-shadow: none;
            @include rtl() {
                padding-left: 3.5rem;
            }
            @include ltr() {
                padding-right: 3.5rem;
            }
            @include respond-below(md) {
                flex-direction: column;
                align-items: flex-start;
                gap: .75rem;
            }
            &::after {
                background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.8501 15.1379V9.13789H0.850098V6.83789H6.8501V0.837891H9.1501V6.83789H15.1501V9.13789H9.1501V15.1379H6.8501Z' fill='%23989FB2'/%3E%3C/svg%3E%0A");
                background-position: center;
                transform: none;
                @include center("v");
                @include rtl() {
                    left: 1.3rem;
                }
                @include ltr() {
                    right: 1.3rem;
                }
            }
            &:not(.collapsed) {
                color: var(--primary-hover);
                background: transparent;
                &::after {
                    background-image: url("data:image/svg+xml,%3Csvg width='16' height='4' viewBox='0 0 16 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.8501 3.13789H0.850098V0.837891H6.8501H9.1501H15.1501V3.13789H9.1501H6.8501Z' fill='%23989FB2'/%3E%3C/svg%3E%0A");
                }
            }
            @include respond-below(lg) {
                padding: 0.875rem 1.25rem;
                font-size: 0.875rem;
            }
        }
    }
    &__body {
        font-size: 0.875rem;
        padding: 0 1.875rem 1.3rem;
        color: var(--primary);
        @include respond-below(sm) {
            font-size: 0.75rem;
        }
    }
    &__content {
        font-weight: 600;
        h4 {
            font-size: .875rem;
            color: var(--primary-hover);
            margin-bottom: 1rem;
        }
        h5 {
            font-size: .875rem;
            color: var(--light-blue);
            display: flex;
            gap: .5rem;
            align-items: baseline;
            &::before {
                content: "";
                display: inline-block;
                width: 5px;
                height: 5px;
                background-color: var(--light-blue);
                border-radius: 50%;
            }
        }
        p {
            font-size: .875rem; 
        }
    }
}