.toasters {
    position: fixed;
    inset-inline-end: 30px;
    top: 120px;
    z-index: 999;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding-inline-start: 30px;
    @include respond-below(sm) {
        inset-inline-end: 10px;
        padding-inline-start: 10px;
        top: 90px;
    }
}

.toaster {
    display: flex;
    align-items: center;
    background-color: #FFF;
    border-radius: 3px;
    box-shadow: 0 3px 16px rgba(#000, .1);
    padding: 20px 25px;
    border-bottom: 4px solid transparent;
    gap: 2rem;
    position: relative;
    @include respond-below(sm) {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px 15px;
        gap: 10px;
    }
    &__icon {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        flex: 0 0 60px;
        @include respond-below(sm) {
            width: 40px;
            height: 40px;
            flex: 0 0 40px;
            svg {
                max-width: 16px;
                max-height: 16px;
                object-fit: contain;
            }
        }
        // @include respond-below(md) {
        //     flex: 0 0 80px;
        //     width: 80px;
        // }
        // @include respond-below(sm) {
        //     flex: 0 0 60px;
        //     width: 60px;
        // }
    }
    &__message {
        color: #444;
        h4 {
            font-size: 1.25rem;
            font-weight: 600;
            @include respond-below(sm) {
                font-size: 1rem;
            }
        }
        p {
            margin-bottom: 0;
        }
        @include respond-below(sm) {
            font-size: 0.875rem;
        }
    }
    &__close {
        margin-inline-start: auto;
        background: none;
        border: 0;
        opacity: .5;
        transition: opacity .25s ease-in-out;
        &:hover {
            opacity: .8;
        }
        @include respond-below(sm) {
            position: absolute;
            top: 1rem;
            inset-inline-end: 1rem;
        }
    }

    &--danger {
        border-color: #F24E3E;
        background-color: #FFE3E0;
        .toaster__icon {
            background-color: #F24E3E;
            border-radius: 3px;
        }
    }

    &--primary {
        border-color: #0076FD;
        background-color: #C3ECFF;
        .toaster__icon {
            background-color: #0076FD;
            border-radius: 3px;
        }
    }

    &--success {
        border-color: #08A655;
        background-color: #B5E9CC;
        .toaster__icon {
            background-color: #08A655;
            border-radius: 3px;
        }
    }

    &--warning {
        border-color: #FAA103;
        background-color: #FFE3C7;
        .toaster__icon {
            background-color: #FAA103;
            border-radius: 3px;
        }
    }
}