.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    padding: 2rem 0;
    font-size: .875rem;
    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include respond-below(lg) {
            flex-direction: column;
            align-items: center;
            text-align: center;
            gap: 1.5rem;
        }
    }
    &__logos {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 1rem;
        @include respond-above(lg) {
            width: calc(100% / 3);
        }
        @include respond-below(lg) {
            order: 1;
        }
        img {
            height: 50px;
            width: auto;
            max-width: 100%;
        }
    }
    &__links-list {
        display: flex;
        gap: 2.5rem;
        @include respond-above(lg) {
            width: calc(100% / 3);
        }
        @include respond-below(lg) {
            order: 2;
        }
        @include respond-below(md) {
            gap: 1.125rem;
        }
        a {
            color: #FFF;
            text-decoration: none;
            transition: color .25s ease-in-out;
            @include lightMode() {
                color: #2E2E2E;
            }
            &:hover {
                color: var(--light-blue) !important;
            }
        }
    }
    &__social-copywrite {
        text-align: center;
        @include respond-above(lg) {
            width: calc(100% / 3);
        }
        @include respond-below(lg) {
            order: 3;
        }
    }
    &__social-list {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 1.15rem;
        margin-bottom: 1.5rem;
    }
    &__social-item {
        display: inline-block;
        width: 25px;
        height: 25px;
        background: center center no-repeat;
        background-size: 18px 18px;
        transition-duration: 0s;
        &:hover {
            transition: transform .5s ease-in-out;
            transform: rotate(360deg);
        }
