.initiatives-box {
    background: #fff;
    box-shadow: 0px 3px 25px rgba(#000, 0.05);
    padding: 1.25rem;
    @include respond-below(lg) {
        margin-bottom: 2rem;
    }
    @include respond-below(sm) {
        padding: 1rem 0.5rem;
    }
    &__photo {
        margin-bottom: 1.25rem;
        display: block;
        img{
            width: 100%;
            aspect-ratio: 2.25;
            object-fit: cover;
        }
    }
    &__title {
        font-size: 1.5rem;
        color: var(--primary-hover);
        margin-bottom: 1.25rem;
        display: block;
        font-weight: 700;
        transition: all ease-in-out 0.25s;
        text-decoration: none;
        &:hover {
            color: var(--dark-blue)
        }
    }
    &> p{
        font-size: 0.875rem;
        line-height: 26px;
        color: #2A346C;
        margin-bottom: 1.25rem;
    }
    &__list {
        padding: 0;
        margin: 0;
        font-size: 0.875rem;
        color: #2A346C;
        list-style: none;
        li {
            margin-bottom: 1.25rem;
            display: flex;
            gap: 0.875rem;
            align-items: center;
            &:not(:last-child) {
                margin-bottom: 1.25rem;
            }
        }

    }
}